@import 'variables';
@import 'mixins';


.main-container {

    .panel {

        @include media-breakpoint-up($md) {
            height: 100vh;
        }
    
        background-color: $white;
    }

    .landing-panel {
        @include flex-row-wrap;
        @include flex-center;

        background-image: url('./../images/ba_taranto_lg.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        // -webkit-background-size: cover;
        background-position: 20%;
        background-attachment: fixed;

        padding-top: 20vh;

        @include media-breakpoint-down($lg) {
            background-image: url('./../images/ba_taranto_md.jpg');
            background-position: 70%;
            height: 100vh;

            padding-top: 20vh;
        }

        @include media-breakpoint-down($md) {
            background-image: url('./../images/ba_taranto_md.jpg');
            background-position: 70%;
            height: 100vh;
        }

        @include media-breakpoint-down($sm) {
            background-image: url('./../images/ba_taranto_sm.jpg');
            background-position: 70%;
        }

        h1 {
            @include flex-column;
            @include flex-center;

            min-width: 70vw;

            span {
                color: $white;
                font-weight: bold;
                font-family: $font-cursive!important;
            }

            #name-left {
                align-self:flex-start;

                @include media-breakpoint-down($lg) {
                    align-self:center;
                }

            }
            #name-center {
                align-self:center;
            }
            #name-right {
                align-self:flex-end;
                margin-right: 10%;

                @include media-breakpoint-down($lg) {
                    align-self:center;
                    margin: 0;
                }

            }
        }
    }

    .image-card-panel {
        background-color: $white!important;

        @include media-breakpoint-down($lg) {
            @include flex-row;
            @include flex-center;

            padding: 2rem;

            height: 70vh;
        }

        .card {
            display: flex;
            flex-flow: column nowrap;
            justify-content: space-evenly;
            align-items: center;

            width: 40vw;
            height: 40vh;

            padding: 3rem;

            background-image: url('./../images/card-bg.png');

            h2 {
                text-align: center;
            }

            p {
                text-align: center;
            }

            @include media-breakpoint-down($lg) {
                min-width: 100%;
                max-width: 100%;
                min-height: 50vh;
            }

            @include media-breakpoint-down($sm) {
                h2 {
                    font-size: $fnt-sz-sm;
                }

                p {
                    font-size: $fnt-sz-sm;
                }
            }
        }
    }

    .quote-panel {

        background-color: $white!important;

        @include media-breakpoint-down($md) {
            height: 70vh;
            min-height: 320px;
        }

        @include media-breakpoint-down($sm) {
            height: 80vh;
        }

        article {

            height: 100%;

            blockquote {
                position: absolute;
                top:0;
                bottom: 0;
                left: 0;
                right: 0;

                font-size: $fnt-sz-md-plus;

                @include media-breakpoint-down($xl) {
                    font-size: $fnt-sz-md;
                }

                @include media-breakpoint-down($lg) {
                    @include flex-column;
                    @include flex-center;
                    font-size: $fnt-sz-md;
                }

                @include media-breakpoint-down($sm) {
                    font-size: $fnt-sz-stnd-plus;
                }
                
                p,cite {
                    color: $dark-purple;

                    @include media-breakpoint-down($md) {
                        text-align: center;
                    }

                }

                #quote-rst-line {
                    position: absolute;
                    top: 10%;
                    left: 20%;

                    @include media-breakpoint-down($lg) {
                        position: relative;
                        top: 0%;
                        left: 0%;
                    }
                }
                #quote-snd-line {
                    position: absolute;
                    top: 35%;
                    right: 5%;

                    @include media-breakpoint-down($lg) {
                        position: relative;
                        top: 0%;
                        right: 0%;
                    }
                }
                #quote-trd-line {
                    position: absolute;
                    top: 60%;
                    right: 10%;

                    @include media-breakpoint-down($lg) {
                        position: relative;
                        top: 0%;
                        right: 0%;
                    }
                }
                cite {
                    position: absolute;
                    bottom: 5%;
                    right: 20%;

                    font-size: 3.5rem;
                    font-style: none;

                    @include media-breakpoint-down($lg) {
                        position: relative;
                        bottom: -10%;
                        right: -20%;

                        font-size: 2.5rem;
                    }

                    @include media-breakpoint-down($sm) {
                        font-size: $fnt-sz-stnd-plus;
                    }
                }
            }
        }


    }

    .gallery-panel {

        background-color: $white!important;

        .gallery-container {

            width: 100%;
            height: 100%;

            @include media-breakpoint-down($md) {

                @include flex-column-wrap;
                @include flex-center;

            }

            .central-assertion {

                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);

                font-size: 4rem;

                @include media-breakpoint-down($md) {
                    position: relative;
                    top: 0;
                    left: 0;
                    transform: translate(0,0);

                    min-height: 15vh;

                    font-size: $fnt-sz-md;
                }

                .enfasi {
                    @include media-breakpoint-down($sm) {
                        font-size: $fnt-sz-md-plus;
                    }
                } 
            }
    
            .gallery-img {
                width: 30vw;
                max-width: 450px;

                @include media-breakpoint-down($lg) {
                    width: 20vw;
                    min-width: 300px;
                }
                @include media-breakpoint-down($md) {
                    max-width: 100%;
                    width: 100%;
                    min-width: 100%;
                }
            }

            #gal-img-1 {
                position: absolute;
                top: 30%;
                right: 10%;

                @include media-breakpoint-down($lg) {
                    top: 20%;
                    right: 5%;
                }

                @include media-breakpoint-down($md) {
                    position: relative;
                    top: 0;
                    right: 0;
                }
            }

            #gal-img-2 {
                position: absolute;
                top: 5%;
                right: 30%;

                @include media-breakpoint-down($lg) {
                    top: 10%;
                    right: 50%;
                }

                @include media-breakpoint-down($md) {
                    position: relative;
                    top: 0;
                    right: 0;
                }
            }

            #gal-img-3 {
                position: absolute;
                bottom: 30%;
                left: 10%;

                @include media-breakpoint-down($lg) {
                    bottom: 20%;
                    left: 5%;
                }

                @include media-breakpoint-down($md) {
                    position: relative;
                    bottom: 0;
                    left: 0;
                }
            }

            #gal-img-4 {
                position: absolute;
                bottom: 5%;
                left: 30%;

                @include media-breakpoint-down($lg) {
                    bottom: 10%;
                    left: 50%;
                }

                @include media-breakpoint-down($md) {
                    position: relative;
                    bottom: 0;
                    left: 0;
                }
            }
        }
    }

    .panel-wedshoots {
        height: 30vh;

        @include media-breakpoint-down($sm) {
            height: 50vh;
        }

        .wedshoots-container {
            height: 100%;

            @include flex-row;
            @include flex-center;

            @include media-breakpoint-down($sm) {
                @include flex-column;
                @include flex-around-center;
            }

            .wedshoots-icon {
                // background-color: aqua;
                min-width: 30%;
                max-width: 30%;

                padding: 0 5rem;

                text-align: right;

                @include media-breakpoint-down($sm) {
                    min-width: 100%;
                    max-width: 100%;

                    padding: 0 0;

                    text-align: center;
                }
            }

            .wedshoots-invite {
                // background-color: aquamarine;
                min-width: 70%;
                max-width: 70%;

                @include media-breakpoint-down($sm) {
                    min-width: 100%;
                    max-width: 100%;

                    padding: 0 1rem;
                }

                p {
                    padding-right: 1rem;

                    font-size: $fnt-sz-stnd-plus;

                    @include media-breakpoint-down($sm) {
                        padding-right: 0;

                        font-size: calc($fnt-sz-stnd + 0.2rem);
                        text-align: center;
                    }
                }
            }
        }
    }

    .lista-panel {

        background-color: $white!important;

        .listaNozze-container {

            width: 100%;
            height: 100%;

            @include flex-row;
            @include flex-center;

            @include media-breakpoint-down($lg) {
                @include flex-column;
            }

            .listaNozze-para {
                // width: 50%;
                min-width: 50%;
                max-width: 50%;
                height: 100%;

                padding: 2.5rem;

                @include flex-column;
                @include flex-around-center;

                @include media-breakpoint-down($lg) {
                    min-width: 100%;
                    max-width: 100%;
                    height: 50%;
                }

                @include media-breakpoint-down($md) {
                    height: 100vh;
                }

                @include media-breakpoint-down($sm) {
                    height: 50vh;
                }

                p {
                    font-size: $fnt-sz-md;
                    text-align: center;

                    @include media-breakpoint-down($xl) {
                        font-size: $fnt-sz-md-minus;
                    }

                    @include media-breakpoint-down($md) {
                        font-size: $fnt-sz-stnd-plus;
                    }

                }

                .lista-capital-letter {
                    color: $pink;
                    font-size: $fnt-sz-md-plus;
                    font-weight: bold;

                    @include media-breakpoint-down($xl) {
                        font-size: $fnt-sz-md;
                    }

                    @include media-breakpoint-down($md) {
                        font-size: $fnt-sz-md-minus;
                    }
                
                    @include media-breakpoint-down($sm) {
                        font-size: $fnt-sz-stnd-plus;
                    }
                }
            }

            .listaNozze-img {
                min-width: 50%;
                max-width: 50%;
                height: 100%;

                background-image: url('./../images/seychelles_2_large.jpg');
                // -webkit-background-image: url('./../images/seychelles_2_large.jpg');
                background-repeat: no-repeat;
                background-size: 80%;
                // -webkit-background-size: 80%;
                background-position-y: 40%;
                background-position-x: 150%;
                background-attachment: fixed;

                @include media-breakpoint-down($xl) {
                    background-size: 110%;
                    // -webkit-background-size: 110%;
                    background-position-x: -190%;
                    background-position-y: 100%;
                }

                @include media-breakpoint-down($lg) {
                    min-width: 100%;
                    max-width: 100%;
                    height: 50%;

                    background-image: url('./../images/seychelles_3_medium.jpg');
                    background-size: 100%;
                    // -webkit-background-size: 100%;
                    background-position-y: 100%;
                }

                @include media-breakpoint-down($md) {
                    height: 100vh;
                    background-size: 100%;
                    // -webkit-background-size:100%;
                    background-position-y: 60%;
                }

                @include media-breakpoint-down($sm) {
                    height: 50vh;
                    background-size: 160%;
                    // -webkit-background-size: 160%;
                    background-position-y: 105%;
                    background-position-x: 60%;
                }
            }
        }
    }

}



